div.arrow {
  box-sizing: border-box;
  width: 6vmin;
  height: 6vmin;
  margin: auto;
  display: block;
  transform: rotate(135deg);
}

div.arrow:before {
  content: "";
  transform-origin: 100% 0;
  border: .8vmin solid #fafafa;
  border-width: .8vmin .8vmin 0 0;
  width: 100%;
  height: 100%;
  transition: all .2s;
  display: block;
}

div.arrow:after {
  content: "";
  float: left;
  transform-origin: 100% 0;
  border: 0 solid #fafafa;
  border-right-width: .8vmin;
  width: 100%;
  height: 100%;
  transition: all .2s;
  position: relative;
  top: -100%;
}

div.arrow:hover:after {
  border-color: #8dc4e2;
  height: 120%;
  transform: rotate(45deg);
}

div.arrow:hover:before {
  border-color: #8dc4e2;
  transform: scale(.8);
