.c-compare {
  --h: 9;
  --m: .2rem 0;
  --w: 16;
  --thumb-bgc: var(--border);
  --thumb-bgc-focus: transparent;
  --thumb-w: 1rem;
  max-width: 60vw;
  margin: var(--m);
  border-radius: 1rem;
  margin: auto;
  position: relative;
}

.c-compare:after {
  content: "";
  padding-bottom: calc((var(--h) / var(--w)) * 100%);
  display: block;
}

.c-compare__left, .c-compare__right {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: absolute;
}

.c-compare__left {
  clip-path: polygon(0% 0%, var(--value) 0%, var(--value) 100%, 0% 100%);
}

.c-compare__right {
  clip-path: polygon(100% 0%, var(--value) 0%, var(--value) 100%, 100% 100%);
}

.c-compare__range {
  box-sizing: block;
  background-color: #0000;
  border: 0 #0000;
  outline: none;
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: inherit;
  position: absolute;
  top: 0;
}

.c-compare__range::-moz-range-thumb {
  cursor: ew-resize;
  height: 100%;
  width: var(--thumb-w);
  background-color: #0000;
}

.c-compare__range::-webkit-slider-thumb {
  cursor: ew-resize;
  height: 100%;
  width: var(--thumb-w);
  background-color: #0000;
}

.c-compare__range:focus::-webkit-slider-thumb {
  background-color: #0000;
  box-shadow: 0 0 0 3px #0000;
}

.c-compare__range:focus::-moz-range-thumb {
  background-color: #0000;
  box-shadow: 0 0 0 3px #0000;
}

.c-compare__range::-moz-range-track {
  box-sizing: border-box;
  background: 0 0 / 100%;
}

.c-compare__range::-webkit-slider-runnable-track {
  box-sizing: border-box;
  background: 0 0 / 100%;
  height: 100%;
}

.c-compare__range {
  appearance: none;
}

.c-compare__range::-webkit-slider-runnable-track {
  appearance: none;
}

.c-compare__range::-webkit-slider-thumb {
  appearance: none;
}

